	import uniRate from '@/components/uni-rate/uni-rate.vue'
	import uniNavBar from "@/components/uni-nav-bar/uni-nav-bar.vue"
	import customSwiper from '@/components/blackmonth-swiper/index'
	import uniCalendar from '@/components/uni-calendar/uni-calendar.vue'
	import uniIcons from '@/components/uni-icons/uni-icons.vue'
	// 查看图片详情（未使用）
	import imgPreview from '@/components/img-preview/img-preview.vue'
	
	const $beingBefore = 'blue';
	const $being = '#22df1d';
	const $wait = '#d8df1d';
	const $finish = '#df1d1d';
	
	function nowDate(){
		let nowDate = new Date();
		return nowDate.getFullYear() + '-' + ('0'+(nowDate.getMonth() + 1)).slice(-2) + '-' + ('0'+nowDate.getDate()).slice(-2);
	}
	var sourceType = [
		['camera'],
		['album'],
		['camera', 'album']
	]
	var sizeType = [
		['compressed'],
		['original'],
		['compressed', 'original']
	]
	export default {
		components: {
			uniRate,
			uniNavBar,
			customSwiper,
			uniCalendar,
			uniIcons,
			imgPreview
		},
		data() {
			return {
				userType:getApp().globalData.userType,
				taskInfo:{
					pkId:1,
					name:'任务1',
					personId:1,
					personName:'飞手1',
					personPhone:'13888888888',
					deviceId:1,
					deviceName:'设备1',
					startTime:'2020-01-01 12:00',
					endTime:'2020-01-01 13:00',
					type:"timeliness",
					taskDesc:"任务描述",
					status:"being",
					flyTime:"10:00",
					mapType:"area",
					mapList:[
						
					],
					cycleType:'date',
					cycleList:[
						{
							id:1,
							date:"1",
							week:"1",
							time:"9:00"
						},
						{
							id:2,
							date:"2",
							week:"2",
							time:"12:00"
						},
						{
							id:2,
							date:"3",
							week:"7",
							time:"13:00"
						}
					],
					approveList:[],
					imageList:[{
								type: 'image',
								url: '/static/blackmonth-swiper/DSC09188.JPG'
							}, {
								type: 'image',
								url: '/static/blackmonth-swiper/DSC09189.JPG',
							},  {
								type: 'image',
								url: '/static/blackmonth-swiper/DSC09190.JPG'
							}, {
								type: 'image',
								url: '/static/blackmonth-swiper/DSC09191.JPG'
							}, {
								type: 'image',
								url: '/static/blackmonth-swiper/DSC09192.JPG'
							}, {
								type: 'image',
								url: '/static/blackmonth-swiper/DSC09193.JPG'
							}],
					videoList:[
						{
							url:"https://img.cdn.aliyun.dcloud.net.cn/guide/uniapp/%E7%AC%AC1%E8%AE%B2%EF%BC%88uni-app%E4%BA%A7%E5%93%81%E4%BB%8B%E7%BB%8D%EF%BC%89-%20DCloud%E5%AE%98%E6%96%B9%E8%A7%86%E9%A2%91%E6%95%99%E7%A8%8B@20181126-lite.m4v"
						},
						{
							url:"https://img.cdn.aliyun.dcloud.net.cn/guide/uniapp/%E7%AC%AC1%E8%AE%B2%EF%BC%88uni-app%E4%BA%A7%E5%93%81%E4%BB%8B%E7%BB%8D%EF%BC%89-%20DCloud%E5%AE%98%E6%96%B9%E8%A7%86%E9%A2%91%E6%95%99%E7%A8%8B@20181126-lite.m4v"
						},
					],
					referenceList:[],
					satisfaction:0,
					satisfactionDesc:'描述'
				},
				date:nowDate(),
				status:"已结束",
				statusCode:4,
				submitText:"提交",
				action: 1,
				latitude: 39.119929,
				longitude: 117.752705,
				rateColor: "#b5b5b5",
				rateActiveColor: "#1d94df",
				rateValue: 5,
				isDetail:false,
				video:null,
				imageList: [],
				imageDetail:false,
				current:0,
				videoSrc:'',
				sourceTypeIndex: 1,
				sourceType: ['拍照', '相册', '拍照或相册'],
				sizeTypeIndex: 2,
				sizeType: ['压缩', '原图', '压缩或原图'],
				countIndex: 8,
				count: [1, 2, 3, 4, 5, 6, 7, 8, 9]
			};
		},
		methods: {
			active(action) {
				this.action = action;
				this.video.pause();
			},
			rateChange(value) {
				this.rateValue = value.value;
			},
			back(){
				uni.navigateBack();
			},
			submit(){
				
			},
			changeDate(){
				this.$refs.calendar.open()	
			},
			dateConfirm(){
				
			},
			dateClose(){
				
			},
			taskShare(){
				uni.showModal({
					content: '是否分享该任务？',
					showCancel: true,
					cancelText: '取消',
					confirmText: '确认',
					success: res => {
						
					}
				});
			},
			clickPic(index){
// 				this.current = index;
// 				this.imageDetail = true;
				uni.previewImage({
                    urls: this.taskInfo.imageList.map((e)=>{return e.url}),
					current:this.taskInfo.imageList.map((e)=>{return e.url})[index]
                });
			},
			closeImage(){
				this.imageDetail = false;
			},
			chooseImage: async function() {
					if (this.imageList.length === 9) {
						let isContinue = await this.isFullImg();
						console.log("是否继续?", isContinue);
						if (!isContinue) {
							return;
						}
					}
					uni.chooseImage({
						sourceType: sourceType[this.sourceTypeIndex],
						sizeType: sizeType[this.sizeTypeIndex],
						count: this.imageList.length + this.count[this.countIndex] > 9 ? 9 - this.imageList.length : this.count[this.countIndex],
						success: (res) => {
							this.imageList = this.imageList.concat(res.tempFilePaths);
						}
					})
			},
			isFullImg: function() {
				return new Promise((res) => {
					uni.showModal({
						content: "已经有9张图片了,是否清空现有图片？",
						success: (e) => {
							if (e.confirm) {
								this.imageList = [];
								res(true);
							} else {
								res(false)
							}
						},
						fail: () => {
							res(false)
						}
					})
				})
			},
			previewImage: function(e) {
				var current = e.target.dataset.src
				uni.previewImage({
					current: current,
					urls: this.imageList
				})
			},
			chooseVideo: function () {
				uni.chooseVideo({
					sourceType: sourceType[this.sourceTypeIndex],
					success: (res) => {
						this.src = res.tempFilePath
					}
				})
			},
			/**
			 * 修改导航栏buttons
			 * index[number] 修改的buttons 下标索引，最右边索引为0
			 * text[string] 需要修改的text 内容
			 */
			setStyle(index, text,color) {
				let pages = getCurrentPages();
				let page = pages[pages.length - 1];
				if (text.length > 3) {
					text = text.substr(0, 3) + '...';
				}














			},
			_ready(){
				this.video=uni.createVideoContext('video1', this)
				switch (this.taskInfo.status){
					case "waitApprove":
						this.status = "待审批"
						this.statusCode = 1
						this.submitText='关闭'
						this.titleColor = $beingBefore
						this.submit = function(){
							uni.navigateBack()
						}
						break;
					case "waitStart":
						this.status = "通过"
						this.statusCode = 2
						this.submitText='开始任务'
						this.titleColor = $beingBefore
						this.submit = function(){
							this._ready()
						}
						break;
					case "noApprove":
						this.status = "拒绝"
						this.statusCode = 3
						this.submitText='修改'
						this.titleColor = $finish
						this.submit = function(){
							uni.redirectTo({
								url:"/pages/task/createTask"
							})
						}
						break;
					case "being":
						this.status = "执行中"
						this.statusCode = 4
						this.titleColor = $being
						if(this.userType == 'dronePilot'){
							this.submitText='任务完成'
							this.submit=function(){
								uni.showModal({
									content: '任务是否完成？',
									showCancel: true,
									cancelText: '取消',
									confirmText: '确认',
									success: res => {
										
									}
								});
							}
						}else{
							this.submitText='实时'
							this.submit=function(){
								uni.switchTab({
									url:"/pages/realtime/realtime"
								})
							}
						}
						break;
					case "waitEval":
						this.status = "待评价"
						this.statusCode = 5
						this.submitText='提交'
						this.titleColor = $wait
						this.submit = function(){
							this._ready()
						}
						break;
					case "finish":
						this.status = "已结束"
						this.statusCode = 6
						this.submitText='再次提交'
						this.titleColor = $finish
						this.isDetail=true;
						this.submit = function(){
							uni.redirectTo({
								url:"/pages/task/createTask"
							})
						}
						break;
					default:
						break;
				}
				this.setStyle(0,this.status,this.titleColor);
				uni.setNavigationBarTitle({
				    title: this.taskInfo.name
				});
				this.map = uni.createMapContext("map1", this);
		}
		},
		computed: {
			rateSpan() {
				var rateSpan;
				if(this.rateValue<3){
					rateSpan="不满意"
				}else if(this.rateValue==3){
					rateSpan="一般"
				}else{
					rateSpan="满意"
				}
				return rateSpan
			},
			cycleTypeName() {
				let name = [];
				var me = this;
				this.taskInfo.cycleList.forEach(function(item){
					switch (me.taskInfo.cycleType){
						case "month":
							name.push(`每月 ${item.date}日 ${item.time}`)
						break;
						case "week":
							var weekList = ["一","二","三","四","五","六","日"]
							name.push(`每周${weekList[item.week-1]} ${item.time}`)
						break;
						case "date":
							name.push(`每日 ${item.time}`)
						break;
					}
				})
				return name
			}
		},
		onReady() {
			uni.loadFontFace({
				family: 'ikingFont',
				// 本地字体路径需转换为平台绝对路径






				success() {
					console.log('success');
				},
				fail(e) {
					console.log('fail', JSON.stringify(e));
				}
			});
			this._ready();
			console.log(getApp().globalData.basePath)
		}
	}
